import Model from './model';
import View from './view';

function handleSliderUpdateValue(event: JQuery.TriggeredEvent, newValue: NewValue) {
  const { model, view } = event.data.controller;
  model.updateValue(newValue);
  view.update();

  view.$slider.trigger('slide', {
    scope: view.scope,
    from: model.from,
    to: model.to,
  });
}

function handleSliderSet(event: JQuery.TriggeredEvent, sliderSet: SliderSet) {
  const { model, view } = event.data.controller;

  if (sliderSet.start !== undefined) {
    model.setStart(sliderSet.start);
  }

  if (sliderSet.end !== undefined) {
    model.setEnd(sliderSet.end);
  }

  if (sliderSet.step !== undefined) {
    model.setStep(sliderSet.step);
  }

  if (sliderSet.from !== undefined && sliderSet.to !== undefined) {
    if (model.end > model.start) {
      if (sliderSet.to < sliderSet.from) {
        if (model.to < sliderSet.to) {
          model.setTo(sliderSet.from);
          model.setFrom(sliderSet.to);
        } else {
          model.setFrom(sliderSet.to);
          model.setTo(sliderSet.from);
        }
      } else if (model.to < sliderSet.from) {
        model.setTo(sliderSet.to);
        model.setFrom(sliderSet.from);
      } else {
        model.setFrom(sliderSet.from);
        model.setTo(sliderSet.to);
      }
    } else if (sliderSet.to > sliderSet.from) {
      if (model.to > sliderSet.to) {
        model.setTo(sliderSet.from);
        model.setFrom(sliderSet.to);
      } else {
        model.setFrom(sliderSet.to);
        model.setTo(sliderSet.from);
      }
    } else if (model.to > sliderSet.from) {
      model.setTo(sliderSet.to);
      model.setFrom(sliderSet.from);
    } else {
      model.setFrom(sliderSet.from);
      model.setTo(sliderSet.to);
    }
  } else if (sliderSet.to !== undefined) {
    model.setTo(sliderSet.to);
  } else if (sliderSet.from !== undefined) {
    model.setFrom(sliderSet.from);
  }

  if (sliderSet.vertical !== undefined) {
    view.setVertical(sliderSet.vertical);
  }

  if (sliderSet.range !== undefined) {
    model.setRange(sliderSet.range);
  }

  if (sliderSet.tipView !== undefined) {
    view.setTipView(sliderSet.tipView);
  }

  if (sliderSet.scaleView !== undefined) {
    view.setScaleView(sliderSet.scaleView);
  }

  if (sliderSet.barView !== undefined) {
    view.setBarView(sliderSet.barView);
  }

  view.update();
}

export default class Controller {
  model: Model;

  view: View;

  constructor({
    model = Model.blank(),
    view = View.blank(),
  } = {}) {
    this.model = model;
    this.view = view;
  }

  init() {
    this.view.$slider.on(
      'update-value',
      null,
      { controller: this },
      handleSliderUpdateValue,
    );

    this.view.$slider.trigger('slide', {
      scope: this.view.scope,
      from: this.model.from,
      to: this.model.to,
    });

    this.view.$slider.on(
      'set',
      null,
      { controller: this },
      handleSliderSet,
    );
  }
}
