import $ from 'jquery';

function handleThumbMousemove(event: JQuery.TriggeredEvent) {
  let position = 0;

  const { thumb } = event.data;

  if (thumb.vertical) {
    if (event.clientY) {
      position = event.clientY - thumb.shift - thumb.$container.offset().top;
    }
  } else if (event.clientX) {
    position = event.clientX - thumb.shift - thumb.$container.offset().left;
  }

  if (position < 0) {
    position = 0;
  }

  let edge:number;

  if (thumb.vertical) {
    edge = thumb.$container.outerHeight() - thumb.$thumb.outerHeight();
  } else {
    edge = thumb.$container.outerWidth() - thumb.$thumb.outerWidth();
  }

  if (position > edge) {
    position = edge;
  }

  if (thumb.vertical) {
    thumb.$thumb.css('top', `${position}px`);
  } else {
    thumb.$thumb.css('left', `${position}px`);
  }

  const part = thumb.vertical ? (edge - position) : position;

  /* eslint-disable-next-line */ /* Thumb.indent - это сеттер */
  thumb.indent = part / edge;

  thumb.$container.trigger('update-value', {
    part,
    full: edge,
    previousValue: thumb.value,
  });
}

function handleThumbMouseup(event: JQuery.TriggeredEvent) {
  $(document).off('mousemove pointermove', handleThumbMousemove);
  $(document).off('mouseup pointerup', handleThumbMouseup);

  const { thumb } = event.data;

  /* eslint-disable-next-line */ /* Thumb.dragging - это сеттер */
  thumb.dragging = false;
  thumb.fallIntoPlace();

  thumb.$container.trigger('update-value', {
    part: thumb.value,
    full: 0,
    previousValue: 0,
  });
}

function handleThumbMousedown(event: JQuery.TriggeredEvent) {
  const { thumb } = event.data;

  let shift:number = 0;

  if (thumb.vertical) {
    if (event.clientY) {
      shift = event.clientY - thumb.$thumb.offset().top;
    }
  } else if (event.clientX) {
    shift = event.clientX - thumb.$thumb.offset().left;
  }
  /* eslint-disable-next-line */ /* Thumb.shift - это сеттер */
  thumb.shift = shift;

  $(document).on(
    'mousemove pointermove',
    null,
    { thumb },
    handleThumbMousemove,
  );

  $(document).on(
    'mouseup pointerup',
    null,
    { thumb },
    handleThumbMouseup,
  );

  /* eslint-disable-next-line */ /* Thumb.dragging - это сеттер */
  thumb.dragging = true;
}

function handleThumbKeydown(event: JQuery.TriggeredEvent) {
  const { keyCode } = event;
  const { thumb } = event.data;

  if (typeof keyCode === 'number') {
    if (keyCode > 36 && keyCode < 41) {
      event.preventDefault();

      thumb.setKeyCode(keyCode);

      if (keyCode === 38 || keyCode === 39) {
        thumb.$container.trigger('update-value', {
          full: -1,
          part: 0,
          previousValue: thumb.value,
        });
      } else {
        thumb.$container.trigger('update-value', {
          full: -2,
          part: 0,
          previousValue: thumb.value,
        });
      }
    }
  }
}

function handleThumbKeyup(event: JQuery.TriggeredEvent) {
  const { thumb } = event.data;

  thumb.setKeyCode(0);
}

function handleThumbDragstart() {
  return false;
}

export default class Thumb {
  $thumb: JQuery = $();

  $container: JQuery = $();

  $tip: JQuery = $();

  vertical: boolean = false;

  value: number = 0;

  position: number = 0;

  drag: boolean = false;

  offset: number = 0;

  tip: boolean = false;

  units: string = '';

  dent: number = 0;

  keyCode: number = 0;

  constructor({
    $thumb = $(),
    $container = $(),
    vertical = false,
    value = 0,
    position = 0,
    tip = false,
    units = '',
  } = {}) {
    this.$thumb = $thumb;
    this.$container = $container;
    this.vertical = vertical;
    this.value = value;
    this.position = position;
    this.dragging = false;
    this.shift = 0;
    this.tip = tip;
    this.units = units;
    this.indent = 0;
    this.keyCode = 0;
  }

  public init(): void {
    this.$tip = $('.js-slider__tip', this.$thumb);

    this.$thumb
      .on(
        'mousedown pointerdown',
        null,
        { thumb: this },
        handleThumbMousedown,
      )
      .on(
        'keydown',
        null,
        { thumb: this },
        handleThumbKeydown,
      )
      .on(
        'keyup',
        null,
        { thumb: this },
        handleThumbKeyup,
      );

    this.$thumb.on('dragstart', handleThumbDragstart);

    this.update({
      vertical: this.vertical,
      position: this.position,
      value: this.value,
      tip: this.tip,
    });
  }

  public update({
    vertical = false,
    position = 0,
    value = 0,
    tip = false,
  } = {}):void {
    this.vertical = vertical;
    this.position = position;
    this.value = value;
    this.tip = tip;

    if (this.tip) {
      this.$tip.css('display', 'block');
    } else {
      this.$tip.css('display', 'none');
    }
    this.$tip.text(`${new Intl.NumberFormat('ru-RU').format(this.value)}${this.units}`);

    this.fallIntoPlace();
  }

  public set shift(value: number) {
    this.offset = value;
  }

  public get shift():number {
    return this.offset;
  }

  public set dragging(dragging: boolean) {
    this.drag = dragging;
    if (dragging) {
      this.$thumb.addClass('slider__thumb_dragging');
    } else {
      this.$thumb.removeClass('slider__thumb_dragging');
    }
  }

  public get dragging():boolean {
    return this.drag;
  }

  public fallIntoPlace():void {
    if (!this.dragging) {
      if (this.vertical) {
        this.$thumb.css({
          left: '0',
          top: `${100 - this.position * 100}%`,
        });
        this.indent = this.position;
      } else {
        this.$thumb.css({
          top: '0',
          left: `${this.position * 100}%`,
        });
        this.indent = this.position;
      }
    }
  }

  public set indent(indent: number) {
    this.dent = indent;
  }

  public get indent():number {
    return this.dent;
  }

  public setKeyCode(keyCode: number) {
    this.keyCode = keyCode;
  }
}
